body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box;font-family:"Noto Sans JP",sans-serif}.App{background-color:#3f3f3f;min-height:100vh;width:100%;padding:2rem}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Noto Sans JP",sans-serif}.expenses{padding:1rem;background-color:#1f1f1f;margin:2rem auto;width:50rem;max-width:95%}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Noto Sans JP",sans-serif}.expenses-list{list-style:none;padding:0}.expenses-list__fallback{color:#eee;text-align:center}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Noto Sans JP",sans-serif}.expense-item{display:flex;justify-content:space-between;align-items:center;padding:.5rem;margin:1rem 0;background-color:#4b4b4b}.expense-item__description{display:flex;flex-direction:column;grid-gap:1rem;gap:1rem;align-items:flex-end;flex-flow:column-reverse;justify-content:flex-start;flex:1 1}@media(min-width: 560px){.expense-item__description{flex-direction:row;align-items:center;justify-content:flex-start;flex:1 1}}.expense-item h2{color:#3a3a3a;font-size:1rem;flex:1 1;margin:0 1rem;color:#eee}@media(min-width: 560px){.expense-item h2{font-size:1.25rem}}.expense-item__price{font-size:1rem;font-weight:bold;color:#eee;background-color:#40005d;border:1px solid #eee;padding:.5rem;border-radius:12px}@media(min-width: 560px){.expense-item__price{font-size:1.25rem;padding:.5rem 1.5rem}}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Noto Sans JP",sans-serif}.expense-date{display:flex;flex-direction:column;width:5.5rem;height:5.5rem;border:1px solid #eee;background-color:#2a2a2a;color:#eee;border-radius:12px;align-items:center;justify-content:center}.expense-date__day{font-size:1.5rem;font-weight:bold}.expense-date__month{font-size:.75rem;font-weight:bold}.expense-date__year{font-size:.75rem}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Noto Sans JP",sans-serif}.card{border-radius:12px;box-shadow:0 1px 8px rgba(0,0,0,.25)}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Noto Sans JP",sans-serif}.expenses-filter{color:#eee;padding:0 1rem}.expenses-filter__control{display:flex;width:100%;align-items:center;justify-content:space-between;margin:1rem 0}.expenses-filter label{font-weight:bold;margin-bottom:.5rem}.expenses-filter select{font:inherit;padding:.5rem 3rem;font-weight:bold;border-radius:6px}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Noto Sans JP",sans-serif}.chart{padding:1rem;border-radius:12px;background-color:#f8dfff;text-align:center;display:flex;justify-content:space-around;height:10rem}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Noto Sans JP",sans-serif}.chart-bar{height:100%;display:flex;flex-direction:column;align-items:center}.chart-bar__inner{height:100%;width:100%;border:1px solid #2a2a2a;border-radius:12px;background-color:#c3b4f3;overflow:hidden;display:flex;flex-direction:column;justify-content:flex-end}.chart-bar__fill{background-color:#40005d;width:100%;transition:all .3s ease-out}.chart-bar__label{font-weight:bold;font-size:.5rem;text-align:center}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Noto Sans JP",sans-serif}.new-expense{background-color:#a892ee;padding:1rem;margin:0 auto;width:50rem;max-width:95%;border-radius:12px;text-align:center;box-shadow:0 1px 8px rgba(0,0,0,.25)}.new-expense button{font:inherit;cursor:pointer;padding:1rem 2rem;border:1px solid #40005d;background-color:#40005d;color:#eee;border-radius:12px;margin-right:1rem}.new-expense button:hover,.new-expense button:active{background-color:#510674;border-color:#510674}.new-expense button.alternative{color:#220131;border-color:transparent;background-color:transparent}.new-expense button.alternative:hover,.new-expense button.alternative:active{background-color:#ddb3f8}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Noto Sans JP",sans-serif}.new-expense__controls{display:flex;flex-wrap:wrap;grid-gap:1rem;gap:1rem;margin-bottom:1rem;text-align:left}.new-expense__control label{font-weight:bold;margin-bottom:.5rem;display:block}.new-expense__control input{font:inherit;padding:.5rem;border-radius:6px;border:1px solid #ccc;width:20rem;max-width:100%}.new-expense__actions{text-align:right}
